import React, {useState, useEffect} from 'react';
import {Navbar, Nav, Badge, NavItem, NavDropdown, Item} from 'react-bootstrap';
import {NavLink, Link } from 'react-router-dom';
import './styles/Navbar.css';
import firebase from 'firebase';
import {admin} from './const/admin';
import {connect} from 'react-redux';
import {addUser} from './state/user';
import {setIsLogged} from './state/user'
import Home from './views/Home'

const styles = {
  welcom: {
    display: 'flex',
    alignItems: 'center',
    color: 'yellow',
    isExpanded: false
  }
}

class NavbarMenu extends React.Component {

  state = {
    user: null,
    admin: false,
  }

  handleSignOut = () => {
    console.log('Sign Out');
    this.setState({ admin: false });
    this.props._addUser('nothing');
    this.props._setIsLogged(false)
    firebase.auth().signOut();
  }

  componentDidMount() {
    // console.log('componentDidMount');
    // console.log('user', this.state.user);

    const unsubscribe = firebase.auth().onAuthStateChanged(user => {
      // if(user && user.email === 'andis_as@wp.pl') {
      if(user && user.email === admin) {
        this.setState({ admin: true });
      }
      this.setState({ user: user });
    });
    this.setState({ unsubscribe });   
  }

  componentWillUnmount() {
      this.state.unsubscribe();
  }  

  close = () => {
    this.setState({ isExpanded: false });
  }

  render() {
    // console.log(admin);
    return (
      <>
      <Navbar 
        collapseOnSelect 
        expand="sm" 
        bg="dark" 
        variant="dark" 
        expanded={this.state.isExpanded}
      >
        <Navbar.Brand></Navbar.Brand>
        <Navbar.Toggle 
          aria-controls="responsive-navbar-nav" 
          onClick={() => this.setState({ isExpanded: !this.state.isExpanded })}
        />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={NavLink} to="/" className='home'>
              <i className="fas fa-home-lg-alt" style={{fontSize: '22px'}}></i>
            </Nav.Link>
          </Nav>
          <Nav>
              <Nav.Link as={NavLink} to="/img-show" onClick={this.close}> ImgShow </Nav.Link>
              <Nav.Link as={NavLink} to="/chat" onClick={this.close}>Komantarz</Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Navbar>
      </>
    );    
  }

}
const mapDispatchToProps = ({
  _addUser: addUser,
  _setIsLogged: setIsLogged,
})
export default connect(null, mapDispatchToProps)(NavbarMenu);
